import axios from 'axios'
import auth from './auth'

var baseURL = 'http://' + window.location.host + '/api'

const client = axios.create({
  baseURL: baseURL,
})

client.auth = auth(client)

client.install = (Vue) => {
  Object.defineProperty(Vue.prototype, '$request', {
    get () {
      return client
    },
  })
}

export default client